export default function Footer() {
    return (
        <footer id="contact" className="w-full bg-transparent mt-12 md:mt-20 relative overflow-hidden border-t-4 border-black">

            {/* ── TOP BORDER TAPE ── */}
            <div className="w-full h-2 bg-neon-pink" />

            {/* ── MAIN CONTENT ── */}
            <div className="max-w-6xl mx-auto px-6 py-14 md:py-20">

                {/* BIG CTA */}
                <div className="mb-14 md:mb-20">
                    <p className="font-mono text-xs uppercase tracking-[0.3em] text-black/50 mb-4">OPEN TO CHAT</p>
                    <h2 className="text-5xl sm:text-7xl md:text-[7rem] font-black uppercase leading-none tracking-tighter text-black">
                        Let&apos;s<br />
                        <span className="text-neon-pink relative inline-block">
                            Connect
                            {/* Underline stroke */}
                            <span className="absolute -bottom-2 left-0 w-full h-2 bg-neon-pink opacity-40" />
                        </span>
                    </h2>

                    <a
                        href="mailto:mwarrc.dev@gmail.com"
                        className="inline-flex items-center gap-4 mt-10 bg-neon-yellow text-black font-black uppercase text-base md:text-xl px-6 py-4 border-4 border-black shadow-[8px_8px_0px_0px_rgba(255,42,133,1)] hover:shadow-[12px_12px_0px_0px_rgba(255,42,133,1)] hover:-translate-y-1 transition-all"
                    >
                        <svg viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2.5" strokeLinecap="square" className="w-6 h-6 shrink-0" aria-hidden="true">
                            <path d="M4 4h16c1.1 0 2 .9 2 2v12c0 1.1-.9 2-2 2H4c-1.1 0-2-.9-2-2V6c0-1.1.9-2 2-2z" />
                            <polyline points="22,6 12,13 2,6" />
                        </svg>
                        MWARRC.DEV@GMAIL.COM
                    </a>
                </div>

                {/* ── SOCIAL CARDS ── */}
                <div className="border-t-4 border-black/10 pt-12">
                    <p className="font-mono text-xs uppercase tracking-[0.3em] text-black/40 mb-8">Find me on</p>
                    <div className="grid grid-cols-1 sm:grid-cols-3 gap-6">
                        {SOCIALS.map((s) => (
                            <a
                                key={s.label}
                                href={s.url}
                                target="_blank"
                                rel="noreferrer"
                                className={`group flex items-center gap-4 p-5 border-4 ${s.border} ${s.bg} ${s.text} ${s.shadow} ${s.hoverBg} transition-all hover:-translate-y-1 hover:shadow-[10px_10px_0px_0px_rgba(0,0,0,1)]`}
                            >
                                {s.icon}
                                <div className="min-w-0">
                                    <p className="text-[10px] font-mono font-bold uppercase opacity-60 tracking-widest">{s.label}</p>
                                    <p className="text-xl md:text-2xl font-black uppercase tracking-tight truncate">@{s.handle}</p>
                                </div>
                                <span className="ml-auto text-2xl font-black opacity-40 group-hover:opacity-100 group-hover:translate-x-1 transition-all">↗</span>
                            </a>
                        ))}
                    </div>
                </div>

                {/* ── BOTTOM BAR ── */}
                <div className="mt-10 border-t-4 border-black/10 pt-6 flex flex-col sm:flex-row items-center justify-between gap-4 font-mono text-xs uppercase tracking-widest text-black/40">
                    <span>© {new Date().getFullYear()} mwarrc</span>
                    <div className="flex gap-2 items-center">
                        <span className="w-2 h-2 bg-neon-pink inline-block border border-black" />
                        <span>Secure</span>
                        <span className="w-2 h-2 bg-neon-blue inline-block border border-black" />
                        <span>Modular</span>
                        <span className="w-2 h-2 bg-neon-yellow inline-block border border-black" />
                        <span>Astonishing</span>
                    </div>
                </div>
            </div>
        </footer>
    );
}
